}

input[type="text"] {
  font-family: "Baloo Bhaijaan 2";
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  border-radius: 2rem;
  background-color: inherit;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  width: 10vw;
  height: 2vw;
  transition: ease-in-out, width 0.15s ease-in-out;
}

input[type="text"]:focus {
  width: 25vw;
}
input[type="text"]:focus-visible {
  outline: none;
}
@media only screen and (max-width: 600px) {
  input[type="text"] {
    width: 50vw;
    height: 12vw;
  }
  input[type="text"]:focus {
    width: 70vw;
  }
}

